* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-family: arial;
  font-size: 10px;
  color: white;
}
body {
  background-color: rgb(34, 46, 155);
}
.wrapper {
  .containerCenter {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    h1 {
      font-size: 80px;
      margin-bottom: 20px;
    }
    .description {
      font-size: 25px;
    }
  }
}

@media only screen and (max-width: 600px) {
  .wrapper {
    .containerCenter {
      h1 {
        font-size: 50px;
      }
      .description {
        width: 90vw;
        font-size: 20px;
      }
    }
  }
}
